#toastr-container {
  position: fixed;
  z-index: 999999;
  pointer-events: none;
  /*overrides*/
}
#toastr-container * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#toastr-container > div {
  position: relative;
  pointer-events: auto;
  overflow: hidden;
  margin: 0 0 6px;
  padding: 15px 15px 15px 50px;
  width: 300px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  border-radius: 3px 3px 3px 3px;
  background-position: 15px center;
  background-repeat: no-repeat;
  -moz-box-shadow: 0 0 12px #999999;
  -webkit-box-shadow: 0 0 12px #999999;
  box-shadow: 0 0 12px #999999;
  color: #FFFFFF;
  opacity: 0.8;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  filter: alpha(opacity=80);
}
#toastr-container > div.rtl {
  direction: rtl;
  padding: 15px 50px 15px 15px;
  background-position: right 15px center;
}
#toastr-container > div:hover {
  -moz-box-shadow: 0 0 12px #000000;
  -webkit-box-shadow: 0 0 12px #000000;
  box-shadow: 0 0 12px #000000;
  opacity: 1;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  filter: alpha(opacity=100);
  cursor: pointer;
}
#toastr-container > .toastr-info {
  background-image: url("data:image/png;base64,") !important;
}
#toastr-container > .toastr-error {
  background-image: url("data:image/png;base64,") !important;
}
#toastr-container > .toastr-success {
  background-image: url("data:image/png;base64,") !important;
}
#toastr-container > .toastr-warning {
  background-image: url("data:image/png;base64,") !important;
}
#toastr-container.toastr-top-center-nav-margin > div,
#toastr-container.toastr-top-center > div,
#toastr-container.toastr-bottom-center > div {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
}
#toastr-container.toastr-top-full-width-nav-margin > div,
#toastr-container.toastr-top-full-width > div,
#toastr-container.toastr-bottom-full-width > div {
  width: 96%;
  margin-left: auto;
  margin-right: auto;
}
.toastr {
  background-color: #030303;
}
.toastr-success {
  background-color: #51A351;
}
.toastr-error {
  background-color: #BD362F;
}
.toastr-info {
  background-color: #2F96B4;
}
.toastr-warning {
  background-color: #F89406;
}
.toastr-progress {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  background-color: #000000;
  opacity: 0.4;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
  filter: alpha(opacity=40);
}
/*Responsive Design*/
@media all and (max-width: 240px) {
  #toastr-container > div {
    padding: 8px 8px 8px 50px;
    width: 11em;
  }
  #toastr-container > div.rtl {
    padding: 8px 50px 8px 8px;
  }
  #toastr-container .toastr-close-button {
    right: -0.2em;
    top: -0.2em;
  }
  #toastr-container .rtl .toastr-close-button {
    left: -0.2em;
    right: 0.2em;
  }
}
@media all and (min-width: 241px) and (max-width: 480px) {
  #toastr-container > div {
    padding: 8px 8px 8px 50px;
    width: 18em;
  }
  #toastr-container > div.rtl {
    padding: 8px 50px 8px 8px;
  }
  #toastr-container .toastr-close-button {
    right: -0.2em;
    top: -0.2em;
  }
  #toastr-container .rtl .toastr-close-button {
    left: -0.2em;
    right: 0.2em;
  }
}
@media all and (min-width: 481px) and (max-width: 768px) {
  #toastr-container > div {
    padding: 15px 15px 15px 50px;
    width: 25em;
  }
  #toastr-container > div.rtl {
    padding: 15px 50px 15px 15px;
  }
}

